(function() {
  'use strict';

  angular
    .module('playNow')
    .controller('TeamsManagerController', TeamsManagerController);

  function TeamsManagerController($http, $scope, $stateParams, facebookStatus, moment) {

    $scope.facebookStatus = facebookStatus;

    $scope.newEvent = {
      name: "",
      description: ""
    };

    $scope.newMember = {
      id: 0
    };

    $scope.allTeams = [];

    if(facebookStatus.logged) {

      $http.get("/team/getAllTeams").then(function (result) {
        $scope.allTeams = result.data;
      });

    }

    $scope.addMember = function(teamID) {

      $http.defaults.headers.common['teamID'] = teamID;

      $http.post("/team/addMember/"+teamID,{
        userId: $scope.newMember.id
      }).then(function (result) {
        $scope.allTeams = result.data;
      });

    };

    $scope.deleteMember = function(teamID,userID) {

      $http.defaults.headers.common['teamID'] = teamID;

      $http.post("/team/deleteMember/"+teamID,{
        teamMemberId: userID
      }).then(function (result) {
        $scope.allTeams = result.data;
      });

    };

    $scope.candidate = function(userID,teamID,isAccepted) {

      $http.defaults.headers.common['teamID'] = teamID;

      $http.post("/team/manageTeamCandidate/"+userID+"/"+teamID+"/"+isAccepted).then(function (result) {
        $scope.allTeams = result.data;
      });

    };



    $scope.addMessage = function(teamID,messageContent) {

      console.log(messageContent+" "+teamID);

      if (messageContent != "") {

        $http.defaults.headers.common['teamID'] = teamID;

        $http.post("/team/addMessage/"+teamID,{
          content: messageContent,
          addDate: moment()
        }).then(function (result) {
          $scope.allTeams = result.data;
        });

      }

    };



    $scope.createTeam = function() {

      if ($scope.newEvent.name != "") {
        $http.post("/team/create",{
          name: $scope.newEvent.name,
          about: $scope.newEvent.description
        }).then(function (result) {
          $scope.allTeams = result.data;
          $scope.newEvent.name = "";
          $scope.newEvent.description = "";
        });
      }

    };





  }
})();

