(function() {
  'use strict';

  angular
    .module('playNow')
    .controller('EditEventController', EditEventController);

  function EditEventController($http, $scope, $state, $stateParams, facebookStatus, moment) {

    var eventID = $stateParams.eventID;
    $scope.disciplines = [];

    $http.get("/disciplines/all").then(function (result) {
      $scope.disciplines = result.data;
    });

    $scope.sportEvent = {};


    // Map
    var center = {lat: 50.061671, lng: 19.942382 };

    var map = new google.maps.Map(document.getElementById('google-map'), {
      zoom: 12,
      center: center
    });

    var marker = null;

    function placeMarkerAndPanTo(latLng, map) {
      if (marker !=null) marker.setMap(null);
      marker = new google.maps.Marker({
        position: latLng,
        map: map
      });
    }
    if(facebookStatus.logged) {

      $http.get("/events/find/" + eventID).then(function (result) {
        console.log(result.data);
        $scope.sportEvent = result.data;

        var mapLat = parseFloat($scope.sportEvent.coordinates.split(',')[0]);
        var mapLng = parseFloat($scope.sportEvent.coordinates.split(',')[1]);

        marker = new google.maps.Marker({
          position: {lat: mapLat, lng: mapLng},
          map: map
        });

        map.panTo({lat: mapLat, lng: mapLng});

        map.addListener('click', function (e) {
          placeMarkerAndPanTo(e.latLng, map);
        });

      });
    }


    $scope.save = function() {

      $http.defaults.headers.common['eventID'] = eventID;

      $http.post("/events/updateSportsEvent/"+eventID, {
        address: $scope.sportEvent.address.toString() || null,
        disciplineID: parseInt($scope.sportEvent.disciplineID),
        date: moment($scope.sportEvent.date),
        level: $scope.sportEvent.level,
        coordinates: marker.getPosition().lat()+','+marker.getPosition().lng(),
        numberOfFreePlaces: parseInt($scope.sportEvent.numberOfPlayers),
        eventDescription: $scope.sportEvent.description.toString() || null,
        reserveList: $scope.sportEvent.reserveList
      }).then(function (result) {
        $state.go('event',{eventID: eventID});
      });

    };



  }
})();

(function() {
  'use strict';

  angular
    .module('playNow')
    .controller('CreateEventController', CreateEventController);

  function CreateEventController($http, $scope, $state, facebookStatus, moment) {

    $scope.facebookStatus = facebookStatus;

    $scope.disciplines = [];

    $scope.sportEvent = {
      disciplineID: "",
      date: new Date(),
      address: "",
      lat:"",
      lng:"",
      description: "",
      places: 10
    };


    $scope.create = function() {

      $http.post("/events/create", {
        address: $scope.sportEvent.address.toString() || null,
        disciplineID: parseInt($scope.sportEvent.disciplineID),
        date: moment($scope.sportEvent.date),
        lat: marker.getPosition().lat(),
        lng: marker.getPosition().lng(),
        places: parseInt($scope.sportEvent.places),
        description: $scope.sportEvent.description.toString() || null
      }).then(function (result) {
        $state.go('home');
      });
    };

    if(facebookStatus.logged) {

      $http.get("/disciplines/all").then(function (result) {
        $scope.disciplines = result.data;
      });


      // Map
      var center = {lat: 50.061671, lng: 19.942382};
      var mapLat = 50.061671;
      var mapLng = 19.942382;

      var map = new google.maps.Map(document.getElementById('google-map'), {
        zoom: 12,
        center: center
      });


      var marker = new google.maps.Marker({
        position: {lat: mapLat, lng: mapLng},
        map: map
      });

      map.panTo({lat: mapLat, lng: mapLng});

      map.addListener('click', function (e) {
        placeMarkerAndPanTo(e.latLng, map);
      });
    }


    function placeMarkerAndPanTo(latLng, map) {
      if (marker !=null) marker.setMap(null);
      marker = new google.maps.Marker({
        position: latLng,
        map: map
      });
    }

  }
})();

