(function() {
    /*jshint unused:false*/

    'use strict';

    angular
    .module('playNow')
    .directive("facebookBtns", facebookBtns);

    function facebookBtns($window)
    {
        return {
            link: function(scope, element, attrs) {
                element.on('click',function(event) {

                });
            },
            controller: function($scope, $http, $state, Facebook, facebookStatus) {

              $scope.login = function(username, password) {

                $http.post("/api-auth-token", {
                  username: username,
                  password: password
                }).then(function (response) {
                    facebookStatus.token = response.token;
                    facebookStatus.logged = true;
                    $http.defaults.headers.common['Authorization'] = facebookStatus.token;
                    console.log('authorized');
                });
                //facebookStatus.token = "234";
                //facebookStatus.logged = true;
                //$http.defaults.headers.common['Authorization'] = facebookStatus.token;
                //console.log('authorized');
              };

              $scope.logout = function() {
                $http.post("/logout").then(function (response) {
                  facebookStatus.token = null;
                  facebookStatus.logged = false;
                  $http.defaults.headers.common['Authorization'] = null;
                  console.log('authorized');
                });
              }

            },
            template: '<div class="fb" ng-if="!facebookStatus.logged">'+
                      '<md-input-container>'+
                        '<label>Username</label>'+
                        '<input type="text" ng-model="username">'+
                      '</md-input-container>'+
                      '<md-input-container>'+
                        '<label>Password</label>'+
                        '<input type="password" ng-model="password">'+
                      '</md-input-container>'+
                      '<md-input-container>'+
                        '<md-button class="btn-fb md-raised md-primary" ng-click="login(username,password)" >'+
                        'Login'+
                        '</md-button>'+
                        '<md-button class="btn-fb md-raised md-warn" ng-click="logout()" ng-if="facebookStatus.logged">'+
                        'Logout'+
                        '</md-button>'+
                      '</md-input-container>'+
                      '</div>'+
                      '<div class="fb logout" ng-if="facebookStatus.logged">'+
                      '<md-input-container>'+
                      '<md-button class="btn-fb md-raised md-warn" ng-click="logout()" ng-if="facebookStatus.logged">'+
                      'Logout'+
                      '</md-button>'+
                      '</md-input-container>'+
                      '</div>'

        };
    }

})();

